* {
    margin: 0;
    padding: 0;
    font-family: "Trebutchet MS", Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 300;
    color: rgba(0, 0, 0, 0.87);
    outline: none;
    -webkit-transition: .1s;
    -o-transition: .1s;
    transition: .1s;
}

.text-secondary {
    color: rgba(0, 0, 0, 0.54);
}

.text-disabled {
    color: rgba(0, 0, 0, 0.38)
}

body, html, #root {
    width: 100%;
    height: 100%;
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: stretch;
        align-items: stretch;
}

button {
    background-color: rgba(0, 0, 0, 0.12);
    height: 44px;
    max-width: 6em;
    border: 1px solid rgba(0, 0, 0, 0.24);
    line-height: 42px;
    vertical-align: middle;
    padding: 0 1em;
}

button:hover, button:focus, button:active  {
    background-color: rgba(0, 0, 0, 0.24);
    cursor: pointer;
    z-index: 1;
}

button:focus, button:active {
    -webkit-box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.24);
            box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.24);
}

table {
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-bottom-width: 0;
}

table tr {
    height: 42px;
    max-height: 42px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}

table td {
    padding: 0 1em;
    line-height: 42px;
    vertical-align: middle;
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    overflow: hidden;
}

table tr, table thead {
    min-height: 42px;
}

table, table thead, table tbody, table tr {
    display: -ms-flexbox;
    display: flex;
}

table, table thead, table tbody {
    -ms-flex-flow: column;
        flex-flow: column;
}

@media (max-width: 600px) {
    * {
        font-size: 16px;
    }
}




