.App {
    height: 100%;
    max-height: 100%;
    margin: 1em;
}

.App form {
    display: -ms-flexbox;
    display: flex;
    justify-items: center;
    -ms-flex-flow: column;
        flex-flow: column;
}

.App form label, .App .input {
    height: 42px;
    max-height: 42px;
    line-height: 42px;
    vertical-align: middle;
    padding: 0 1em;
    border: none;
    min-width: 3em;
}

.App .input {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.App form label {
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 3em;
    min-width: 3em;
    max-width: 3em;
    text-align: center;
}

.App form label:last-child {
    border-left: 1px solid #e5e5e5;
}

.App form label:first-child {
    border-right: 1px solid #e5e5e5;
}

.App form .row {
    border: 1px solid rgba(0, 0, 0, 0.18);
    border-bottom-width: 0;
}

.App form div.row:last-of-type {
    border-bottom-width: 1px;
}

.App form button[type=submit] {
    margin-top: 1em;
    -ms-flex-item-align: end;
        align-self: flex-end;
}

.App .input:focus {
    -webkit-box-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.12);
            box-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.12);
    z-index: 1;
}

.App .records {
    margin-top: 1em;
    overflow-y: visible;
}

.App .records .date {
    -ms-flex-positive: 2;
        flex-grow: 2;
    overflow-x: scroll;
}



